.ico
  display: inline-block
  vertical-align: middle
  position: relative
  transition: all .3s ease-in-out

  &.is-search
    background: url("../img/ico-search.png")no-repeat
    width: 18px
    height: 18px

  &.is-fb,
  &.is-tw,
  &.is-gp
    width: 30px
    height: 30px

  &.is-fb
    background: url("../img/ico-fb.png")
    background-color: #3a73b7

    &:hover
      background-color: #3a7fd3

  &.is-tw
    background: url("../img/ico-tw.png")
    background-color: #56b6ff

    &:hover
      background-color: #68c1ff

  &.is-gp
    background: url("../img/ico-gp.png")
    background-color: #ff5e3c

    &:hover
      background-color: #ff7353

  &.is-magnifier
    background: url("../img/ico-magnifier.png")
    width: 26px
    height: 26px

  &.is-question
    background: url("../img/ico-question.png")
    width: 26px
    height: 26px

  &.is-info
    background: url("../img/ico-info.png")
    width: 26px
    height: 26px

  &.is-upload
    background: url("../img/ico-upload.png")
    width: 14px
    height: 13px

  &.is-arrow-down
    background: url("../img/ico-arrow-down.png")
    width: 33px
    height: 30px

  &.is-arrow
    background: url("../img/ico-arrow.png")
    width: 10px
    height: 6px